.header {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  background-color: rgba($color: $white, $alpha: 0.95);
  padding: 24px 0 22px;
  border-bottom: 1px solid #e6e6e6;
  z-index: 100;
  .logo-wrapper {
    max-width: 80px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
  }
  .burger {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 40px;
    height: 27px;
    padding: 0;
    border: 0;
    outline: 0;
    background-color: transparent;
    cursor: pointer;
    z-index: 1000;
    span {
      display: inline-block;
      width: 100%;
      height: 4px;
      border-radius: 2px;
      background-color: $black;
      transition: 0.15s;
      position: relative;
      &.top {
        top: 0;
      }
      &.bottom {
        bottom: 0;
      }
    }
    &.active {
      .top {
        transform: rotate(45deg);
        top: calc(50% - 2px);
      }
      .middle {
        opacity: 0;
      }
      .bottom {
        transform: rotate(-45deg);
        bottom: calc(50% - 2px);
      }
    }
  }
  &__list {
    font-family: $font-Open;
    font-weight: 700;
    text-transform: uppercase;
    color: $gray;
    max-width: 420px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    position: relative;
  }
  .overlay {
    display: inline-block;
    position: absolute;
    width: 100vw;
    height: 200vh;
    top: 0;
    right: 0;
    z-index: -5;
    background-color: rgba($color: $black, $alpha: 0.3);
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;
  }
}

@media screen and (max-width: 767px) {
  .header {
    padding: 16px 0;
    .burger {
      display: flex;
    }
    &__list {
      background-color: $white;
      width: 200px;
      height: 200vh;
      position: absolute;
      top: 0;
      right: 0;
      opacity: 0;
      visibility: hidden;
      -webkit-transition: 0.3s;
      transition: 0.3s;
      padding: 90px 30px 30px;
      z-index: 10;
      flex-direction: column;
      border-left: 2px solid $blue;
      justify-content: start;
      &.active {
        opacity: 1;
        visibility: visible;
        // right: 0;
      }
      li {
        &:not(:first-child) {
          margin-top: 20px;
        }
      }
    }
    .overlay {
      &.active {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .header {
    padding: 13px 0;
  }
}

@media (hover: hover) {
  header {
    .nav-link {
      transition: 0.3s;
      &:hover {
        color: $blue;
      }
    }
  }
}
