@import "normalize";
@import "variables";
@import "mixins";
@import "header";
@import "footer";

html {
  scroll-behavior: smooth;
}
* {
  box-sizing: border-box;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
p {
  margin: 0;
}
img {
  object-fit: cover;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.container {
  margin: 0 auto;
  max-width: 1140px;
  padding: 0 15px;
  width: 100%;
}

.protitle {
  font-family: $font-Open;
  font-weight: 700;
  color: $gray;
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 2.1px;
}

.title {
  font-family: $font-Open;
  font-weight: 700;
  color: $gray;
  font-size: 20px;
  line-height: 27px;
}

.subtitle {
  color: $gray;
  font-family: $font-Open;
  font-weight: 400;
  font-size: 13px;
  line-height: 23px;
}

@media screen and (max-width: 991px) {
  .protitle {
    font-size: 18px;
  }
  .title {
    font-size: 16px;
    line-height: 23px;
  }
  .subtitle {
    font-size: 11px;
    line-height: 19px;
  }
}

.btn {
  padding: 0 30px;
  display: inline-block;
  line-height: 40px;
  background-color: $blue;
  color: $white;
  text-transform: uppercase;
  font-family: $font-Open;
  font-weight: 700;
  color: $white;
  font-size: 13px;
  letter-spacing: 0.5px;
  min-width: 150px;
}

@media screen and (max-width: 480px) {
  .btn {
    border-radius: 3px;
  }
}

.logo-wrapper {
  display: inline-block;
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
  }
}
//
body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  position: relative;
  padding-top: 100vh;
  &.active {
    overflow: hidden;
  }
}

main {
  flex: 1 0 auto;
}

.first {
  height: 100vh;
  position: absolute;
  top: 0;
  width: 100%;
  .logo-wrapper {
    max-width: 100px;
  }
  &__content {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    padding: 40px 0 70px;
  }
  &__title {
    color: $white;
    font-family: $font-Bebas;
    font-size: 60px;
    line-height: 70px;
    text-transform: uppercase;
  }
  &__subtitle {
    color: $white;
    font-weight: 600;
    font-size: 16px;
  }
  &__scroll {
    color: $white;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 11px;
    letter-spacing: 3.7px;
  }
}

.story {
  padding: 55px 0;
  &__wrap {
    display: flex;
    margin-top: 80px;
    .content,
    .links {
      flex-basis: 50%;
    }
  }
  .protitle {
    text-align: center;
    position: relative;
    &::after {
      content: "";
      @extend %line;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .content {
    padding-right: 25px;
    .company {
      color: $blue;
    }
    .subtitle {
      margin-top: 30px;
    }
  }
  .links {
    display: flex;
    justify-content: space-evenly;
    a {
      display: inline-block;
      min-height: 225px;
      width: 150px;
      padding: 23px 20px;
      // -webkit-filter: brightness(40%);
      // filter: brightness(40%);
      span {
        font-weight: 800;
        font-size: 22px;
        line-height: 27px;
        text-transform: uppercase;
        color: $white;
        position: relative;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .story {
    .content {
      flex-basis: 65%;
    }
    .links {
      justify-content: space-between;
      flex-basis: 35%;
      a {
        width: 32%;
        padding: 14px 11px;
        span {
          font-size: 18px;
          line-height: 23px;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .story {
    .links {
      flex-direction: column;
      justify-content: start;
      a {
        min-height: auto;
        width: 100%;
        padding: 11px;
        height: 33%;
        &:not(:first-child) {
          margin-top: 5px;
        }
      }
    }
  }
}

.capabilities {
  padding: 50px 0 100px;
  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .protitle {
    position: relative;
    &::after {
      content: "";
      @extend %line;
      left: 0;
    }
  }
  .subtitle {
    max-width: 450px;
  }
  .splide {
    margin: 75px auto;
    &__arrow {
      color: $black;
      font-size: 46px;
      background-color: transparent;
      opacity: 0.85;
      width: auto;
      &--prev {
        left: -9%;
      }
      &--next {
        right: -9%;
      }
    }
    [class*="card"] {
      border: 1px solid $gray-light;
      min-height: 360px;
      padding: 35px 22px;
      .title {
        text-transform: uppercase;
        margin-top: 25px;
      }
      .subtitle {
        margin-top: 15px;
      }
    }
    &__pagination {
      bottom: -25px;
      button {
        transition: 0.3s;
        opacity: 1;
        &.is-active {
          background-color: $blue;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .capabilities {
    .splide {
      [class*="card"] {
        min-height: auto;
        padding: 28px 16px 32px;
      }
    }
    &__content {
      flex-direction: column;
      .protitle {
        &::after {
          left: 50%;
          transform: translateX(-50%);
        }
      }
      .subtitle {
        text-align: center;
        margin-top: 35px;
      }
      .btn {
        margin-top: 25px;
      }
    }
  }
  .splide {
    &__arrows {
      display: none;
    }
  }
}

.want {
  padding: 120px 0 230px;
  text-align: center;
  .title {
    font-size: 30px;
    line-height: 36px;
  }
  .btn {
    margin-top: 35px;
  }
}

.signup {
  background-color: $black-light;
  padding: 30px 0;
  &__content {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .title {
    color: $white;
  }
  .form {
    position: relative;
    max-width: 400px;
    input {
      background-color: $white;
      border: none;
      padding: 10px 165px 10px 15px;
      font-family: $font-Open;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      width: 100%;
      outline: none;
    }
    button {
      border: none;
      position: absolute;
      top: 0;
      right: 0;
      cursor: pointer;
      width: 150px;
    }
  }
}

@media screen and (max-width: 991px) {
  .capabilities {
    padding: 50px 0;
  }
  .want {
    padding: 95px 0 180px;
  }
}

@media screen and (max-width: 767px) {
  .story {
    padding: 45px 0;
    &__wrap {
      margin-top: 60px;
    }
  }
  .capabilities {
    padding: 45px 0 25px;
    .splide {
      margin: 50px auto;
    }
  }
  .want {
    padding: 95px 0 180px;
  }
  .signup {
    &__content {
      display: flex;
      flex-direction: column;
      align-items: center;
      .form {
        margin-top: 20px;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .signup {
    &__content {
    }
    .title {
    }
    .form {
      text-align: center;
      input {
        border-radius: 3px;
        padding: 10px 15px;
      }
      button {
        position: relative;
        margin-top: 10px;
      }
    }
  }
}

@media (hover: hover) {
  .btn {
    transition: 0.3s;
    &:hover {
      background-color: $black;
      color: $white;
      box-shadow: 0 0 5px $black;
      border-radius: 0 5px;
    }
  }
  .first .first__scroll {
    transition: 0.3s;
    &:hover {
      color: $black;
      text-shadow: 0 0 5px $black;
    }
  }
  .story .links a {
    transition: 0.3s;
    position: relative;
    &::before {
      content: "";
      background-color: $blue;
      width: 100%;
      height: 100%;
      position: absolute;
      transition: 0.3s;
      bottom: 0;
      right: 0;
      z-index: -5;
    }
    &:hover {
      &::before {
        bottom: -10px;
        right: -10px;
      }
      span {
        transition: 0.3s;
        color: $black;
      }
    }
  }

  .capabilities {
    .splide {
      &__arrow {
        transition: 0.3s;
        &:hover {
          color: $blue;
        }
      }
    }
  }
}

