.footer {
  font-family: $font-Open;
  .logo-wrapper {
    max-width: 100px;
  }
  .info {
    background-color: $black;
    padding: 40px 0;
    color: $white;
    .text-upp {
      text-transform: uppercase;
    }
    .subtitle {
      color: $white;
    }
    .content {
      display: flex;
      margin: 0 -10px;
      & > div {
        flex-basis: 30%;
        padding: 0 20px;
        .wrap {
          margin-top: 10px;
        }
      }
      .company {
        .wrap {
          .subtitle {
            margin-top: 30px;
          }
        }
      }
      .links {
        .wrap {
          display: flex;
          justify-content: space-between;
        }
        li {
          font-family: $font-Open;
          font-weight: 600;
          font-size: 11px;
          line-height: 25px;
          text-transform: uppercase;
        }
      }
      .contactus {
        flex-basis: 200px;
        .subtitle {
          width: 140px;
          a {
            display: inline-block;
            margin-top: 5px;
          }
        }
      }
      .network {
        margin-top: 34px;
        width: 100px;
        flex-basis: 100px;
        display: flex;
        justify-content: space-between;
      }
    }
  }
  .copyright {
    background-color: $black-light;
    padding: 22px 0;
    text-align: center;
    color: $white;
    font-size: 11px;
  }
}

@media (hover: hover) {
  .footer .info {
    .links {
      a {
        transition: 0.3s;
        &:hover {
          color: $blue;
          text-shadow: 0 0 5px $black;
        }
      }
    }
    .contactus {
      .subtitle {
        a {
          transition: 0.3s;
          &:hover {
            color: $blue;
            text-shadow: 0 0 5px $black;
          }
        }
      }
    }
    .network {
      align-self: flex-start;
      a {
        transition: 0.3s;
        &:hover {
          color: $blue;
          text-shadow: 0 0 5px $black;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .footer {
    .info {
      .content {
        display: flex;
        margin: 0;
        & > div {
          flex-basis: 30%;
          padding: 0 10px;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .footer {
    .info {
      .content {
        flex-wrap: wrap;
        justify-content: center;
        .company {
          flex-basis: 50%;
          .wrap {
            .subtitle {
              margin-top: 10px;
            }
          }
        }
        .contactus {
          flex-basis: 50%;
          text-align: end;
          .wrap {
            display: flex;
            justify-content: end;
          }
        }
        .links {
          display: none;
        }
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .footer {
    .info {
      padding: 30px 0;
      .content {
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
        .company {
          flex-basis: 100%;
        }
        .contactus {
          flex-basis: 100%;
          width: auto;
          text-align: center;
          margin-top: 34px;
          .wrap {
            justify-content: center;
            margin: 0;
          }
          .subtitle {
            width: auto;
          }
        }
        span.subtitle.text-upp {
          display: none;
        }
      }
    }
  }
}
